import { highlightTypes } from "./function-highlighter";

const PropSingle = ({ fullName, type, desc }) => {
    return (
        <div>
            <code className="api-name-text" style={{ whiteSpace: 'nowrap' }}>
                {fullName}<b>: </b><span>{highlightTypes(type)}</span>
            </code>
            <p className="api-paragraph">{desc}</p>
        </div>
    );
}

export default PropSingle;