import React, { useState } from "react";
import {
  Bug,
  Check,
  Flame,
  FlaskConical,
  Icon,
  TriangleAlert,
  X
} from "lucide-react";
import { reelThread } from '@lucide/lab';
import typesInfo from "../types-info";

const highlightType = (type) => {
  const handleWrappedType = (typeStr) => {
    const match = typeStr.match(/^(\{+)([A-Za-z0-9_]+)(\}+)$/);
    if (match) {
      const [_, openBraces, typeName, closeBraces] = match;
      const highlighted = highlightType(typeName);
      return `${openBraces}${highlighted}${closeBraces}`;
    }
    return null;
  };

  if (type.includes("|")) {
    return type.split("|")
      .map(part => highlightType(part.trim()))
      .join(" | ");
  }

  const wrappedResult = handleWrappedType(type);
  if (wrappedResult) return wrappedResult;

  if (type.startsWith("...")) {
    return "..." + highlightType(type.substring(3, type.length))
  }

  if (type.startsWith('Result<')) {
    const closingBracketIndex = type.indexOf('>');
    const innerTypes = type.substring(7, closingBracketIndex);
    const rest = type.substring(closingBracketIndex + 1);

    const str = innerTypes.split(',')
      .map(item => highlightType(item.trim()))
      .join(', ');

    return `<a href="/osgl/api/result" class="global">Result</a><${str}>${highlightType(rest)}`;
  }

  for (const item of typesInfo) {
    if (item.name === type) {
      return `<a href="${item.page}" class="global">${item.name}</a>`;
    }
  }

  const segments = type.split(/([<>])/);
  let result = '';
  let inBrackets = false;

  segments.forEach(segment => {
    if (segment === '<' || segment === '>') {
      result += segment;
      inBrackets = segment === '<';
    } else {
      if (inBrackets) {
        result += segment.split(',')
          .map(part => part.replace(/([A-Za-z0-9_]+)/g, '<span class="global">$1</span>'))
          .join(',');
      } else {
        result += segment.replace(/([A-Za-z0-9_]+)/g, '<span class="global">$1</span>');
      }
    }
  });

  return result;
};

const highlightTypes = (type) => {
  return (
    <span
        dangerouslySetInnerHTML={{
          __html: highlightType(type)
        }}
      />
  )
}

const FunctionSyntaxHighlighter = ({ functionName, params, returnType, isMethod, metadata = {} }) => {
  const [activeTooltip, setActiveTooltip] = useState(null);
  const [hoverTimeout, setHoverTimeout] = useState(null);
  const dotColon = isMethod ? ':' : '.';
  const [className, methodName] = functionName.split(dotColon);
  const coloredFunctionName = methodName
    ? `${className}${dotColon}<span style="color: #10ac84;">${methodName}</span>`
    : functionName;

  const metadataFlags = [
    {
      key: 'Debug',
      display: 'DEBUG',
      icon: <Bug size={12} />,
      tooltip: 'Debug utilities (safe for production but primarily for diagnostics)'
    },
    {
      key: 'Checked',
      display: 'CHECKED',
      icon: <Check size={12} />,
      tooltip: 'Includes runtime validation - useful when you need bounds checking\n(Adds minimal performance overhead)'
    },
    {
      key: 'Unchecked',
      display: 'UNCHECKED',
      icon: <TriangleAlert size={12} />,
      tooltip: 'No runtime validation - only use when absolutely certain of inputs\n(Marginally faster than checked versions)',
      warning: true
    },
    {
      key: 'Experimental',
      display: 'EXPERIMENTAL',
      icon: <FlaskConical size={12} />,
      tooltip: 'API may change between minor versions\n(Use with caution in production)'
    },
    {
      key: 'Deprecated',
      display: 'DEPRECATED',
      icon: <X size={12} />,
      tooltip: 'Will be removed in future versions\n(Migrate to newer alternatives)',
      warning: true
    },
    {
      key: 'ThreadSafe',
      display: 'THREAD-SAFE',
      icon: <Icon size={12} iconNode={reelThread} />,
      tooltip: 'Can be called from any thread\n(No synchronization needed)',
      highlight: true,
    },
    {
      key: 'HotPath',
      display: 'HOT-PATH',
      icon: <Flame size={12} />,
      tooltip: 'Optimized for performance-critical code\n(Avoid expensive operations in callbacks). Usually means no runtime validation.',
    }
  ];

  const highlightComment = (comment) => {
    return `<span class="comment">${comment}</span>`;
  };

  let formattedParams = Object.entries(params)
    .map(([key, value]) => {
      let type = "";
      let comment = "";

      if (typeof value === "string") {
        type = highlightType(value);
      } else if (typeof value === "object" && value.type) {
        type = highlightType(value.type);
        comment = value.comment ? highlightComment(` -- ${value.comment}`) : "";
      }

      return `    ${key}: ${type},${comment}`;
    })
    .join("\n");

  if (formattedParams.length !== 0) {
    formattedParams = "\n" + formattedParams + "\n";
  }

  const output = `${coloredFunctionName}(${formattedParams}) -> ${highlightType(returnType)};`;

  return (
    <div className="function-container">
      {/* Metadata flags */}
      <div className="metadata-flags">
        {metadataFlags.map(flag => (
          metadata[flag.key] && (
            <div
              key={flag.key}
              className={`metadata-flag ${flag.warning ? 'warning' : ''} ${flag.highlight ? 'highlight' : ''}`}
              onMouseEnter={() => {
                clearTimeout(hoverTimeout);
                setActiveTooltip(flag.key);
              }}
              onMouseLeave={() => {
                const timeout = setTimeout(() => {
                  setActiveTooltip(null);
                }, 150);
                setHoverTimeout(timeout);
              }}
            >
              <span className="flag-icon">{flag.icon}</span>
              <span className="flag-text">{flag.display}</span>
              <div className={`custom-tooltip ${activeTooltip === flag.key ? 'visible' : ''}`}>
                {flag.tooltip.split('\n').map((line, i) => (
                  <div key={i}>{line}</div>
                ))}
              </div>
            </div>
          )
        ))}
      </div>

      {/* Function signature */}
      <pre
        className="output"
        dangerouslySetInnerHTML={{
          __html: output
        }}
      />

      <style jsx>{`
        .function-container {
          position: relative;
          margin: 1em 0;
          font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
        }
        .metadata-flags {
          display: flex;
          gap: 0.5em;
          margin-bottom: 0.5em;
          flex-wrap: wrap;
          position: relative;
        }
        .metadata-flag {
          position: relative;
          padding: 0.3em 0.6em;
          background: #2d3436;
          color: #dfe6e9;
          border-radius: 4px;
          font-size: 0.8em;
          font-weight: bold;
          cursor: help;
          display: flex;
          align-items: center;
          gap: 0.4em;
        }
        .metadata-flag.warning {
          background: #d63031;
          color: white;
        }
        .metadata-flag.highlight {
          background: #C74949;
          color: white;
        }
        .flag-icon {
          display: flex;
          margin-right: 0.2em;
        }
        .custom-tooltip {
          position: absolute;
          bottom: 100%;
          left: 50%;
          transform: translateX(-50%);
          background: rgba(0, 0, 0, 0.9);
          color: white;
          padding: 0.8em;
          border-radius: 4px;
          font-size: 0.9em;
          line-height: 1.4;
          font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
          min-width: 260px;
          max-width: 360px;
          z-index: 100;
          margin-bottom: 8px;
          pointer-events: none;
          opacity: 0;
          transition: opacity 0.12s ease-out;
          box-shadow: 0 2px 8px rgba(0,0,0,0.2);
          white-space: pre-line;
          text-align: left;
        }
        .custom-tooltip.visible {
          opacity: 1;
        }
        .custom-tooltip::after {
          content: '';
          position: absolute;
          top: 100%;
          left: 50%;
          transform: translateX(-50%);
          border-width: 6px;
          border-style: solid;
          border-color: rgba(0, 0, 0, 0.9) transparent transparent transparent;
        }
      `}</style>
    </div>
  );
};

export { FunctionSyntaxHighlighter, highlightTypes, highlightType };
