import React, { useState } from "react";
import { 
  Network,
  Cpu,
  Box,
  Layers,
  Package,
  Code,
  Shield
} from "lucide-react";

const ClassMeta = ({ metadata = {} }) => {
  const [activeTooltip, setActiveTooltip] = useState(null);
  const [hoverTimeout, setHoverTimeout] = useState(null);

  // Class metadata flags with icons and colors
  const classMetadataFlags = [
    {
      key: 'inherits',
      display: 'INHERITS',
      icon: <Network size={12} />,
      tooltip: metadata.inherits 
        ? `Inherits from: ${metadata.inherits.join(', ')}`
        : 'No parent classes',
      condition: metadata.inherits && metadata.inherits.length > 0,
      color: 'rgb(45, 52, 54)'
    },
    {
      key: 'core',
      display: 'CORE',
      icon: <Cpu size={12} />,
      tooltip: 'Core library module\n(Required for basic functionality)',
      condition: metadata.core,
      color: 'rgb(45, 52, 54)'
    },
    {
      key: 'abstract',
      display: 'ABSTRACT',
      icon: <Box size={12} />,
      tooltip: 'Abstract base class\n(Cannot be instantiated directly)',
      condition: metadata.abstract,
      color: 'rgb(45, 52, 54)'
    },
    {
      key: 'composite',
      display: 'COMPOSITE',
      icon: <Layers size={12} />,
      tooltip: 'Contains other objects\n(Manages child components)',
      condition: metadata.composite,
      color: 'rgb(45, 52, 54)'
    },
    {
      key: 'singleton',
      display: 'SINGLETON',
      icon: <Package size={12} />,
      tooltip: 'Single instance only',
      condition: metadata.singleton,
      color: 'rgb(45, 52, 54)'
    },
    {
      key: 'interface',
      display: 'INTERFACE',
      icon: <Code size={12} />,
      tooltip: 'Pure interface\n(No implementation, only declarations)',
      condition: metadata.interface,
      color: 'rgb(45, 52, 54)'
    },
    {
      key: 'threadSafe',
      display: 'THREAD-SAFE',
      icon: <Shield size={12} />,
      tooltip: 'Can be passed between threads.',
      condition: metadata.threadSafe,
      color: 'rgb(199, 73, 73)' // Red
    },

    {
      key: 'partialThreadSafe',
      display: 'THREAD-SAFE*',
      icon: <Shield size={12} />,
      tooltip: "Can be passed between threads without issues (only through actor:SendMessage())",
      condition: metadata.partialThreadSafe,
      color: 'rgb(199, 111, 73)' // Red
    }
  ];

  return (
    <div className="class-meta-container">
      {/* Class metadata flags */}
      <div className="metadata-flags">
        {classMetadataFlags.map(flag => (
          flag.condition && (
            <div 
              key={flag.key}
              className="metadata-flag"
              style={{ background: flag.color }}
              onMouseEnter={() => {
                clearTimeout(hoverTimeout);
                setActiveTooltip(flag.key);
              }}
              onMouseLeave={() => {
                const timeout = setTimeout(() => {
                  setActiveTooltip(null);
                }, 150);
                setHoverTimeout(timeout);
              }}
            >
              <span className="flag-icon">{flag.icon}</span>
              <span className="flag-text">{flag.display}</span>
              <div className={`custom-tooltip ${activeTooltip === flag.key ? 'visible' : ''}`}>
                {flag.tooltip.split('\n').map((line, i) => (
                  <div key={i}>{line}</div>
                ))}
              </div>
            </div>
          )
        ))}
      </div>

      <style jsx>{`
        .class-meta-container {
          position: relative;
          font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
        }
        .metadata-flags {
          display: flex;
          gap: 0.5em;
          margin-bottom: 0.5em;
          flex-wrap: wrap;
          position: relative;
        }
        .metadata-flag {
          position: relative;
          padding: 0.3em 0.6em;
          color: white;
          border-radius: 4px;
          font-size: 0.8em;
          font-weight: bold;
          cursor: help;
          display: flex;
          align-items: center;
          gap: 0.4em;
        }
        .flag-icon {
          display: flex;
          margin-right: 0.2em;
        }
        .custom-tooltip {
          position: absolute;
          bottom: 100%;
          left: 50%;
          transform: translateX(-50%);
          background: rgba(0, 0, 0, 0.9);
          color: white;
          padding: 0.8em;
          border-radius: 4px;
          font-size: 0.9em;
          line-height: 1.4;
          font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
          min-width: 260px;
          max-width: 360px;
          z-index: 100;
          margin-bottom: 8px;
          pointer-events: none;
          opacity: 0;
          transition: opacity 0.12s ease-out;
          box-shadow: 0 2px 8px rgba(0,0,0,0.2);
          white-space: pre-line;
          text-align: left;
        }
        .custom-tooltip.visible {
          opacity: 1;
        }
        .custom-tooltip::after {
          content: '';
          position: absolute;
          top: 100%;
          left: 50%;
          transform: translateX(-50%);
          border-width: 6px;
          border-style: solid;
          border-color: rgba(0, 0, 0, 0.9) transparent transparent transparent;
        }
      `}</style>
    </div>
  );
};

export default ClassMeta;