import React, { useEffect } from "react"; // Add useEffect
import { FunctionSyntaxHighlighter } from "./function-highlighter";
import CodeBlock from "../../docs/components/codeblock";

const Func = ({ name, fullName, params, returnType, desc, code, isMethod, metadata }) => {
    const id = name.replace(/\s+/g, "-").toLowerCase();

    useEffect(() => {
        const handleScroll = () => {
            if (window.location.hash === `#${id}`) {
                const element = document.getElementById(id);
                if (element) {
                    element.scrollIntoView({
                        behavior: "smooth",
                        block: "center",
                    });
                }
            }
        };

        handleScroll();

        window.addEventListener("hashchange", handleScroll);

        return () => {
            window.removeEventListener("hashchange", handleScroll);
        };
    }, [id]);

    return (
        <div>
            <code id={id} className="api-name-header">{name}</code>
            <br />
            <FunctionSyntaxHighlighter
                functionName={fullName}
                params={params}
                returnType={returnType}
                isMethod={isMethod}
                metadata={metadata || {}}
            />
            <p className="api-paragraph">{desc}</p>
            {code && <CodeBlock code={code} />}
        </div>
    );
};

export default Func;