import { useEffect } from "react"; // Add useEffect
import PropSingle from "./api-prop-singular";

const Prop = ({ name, fullName, type, desc }) => {
    const id = name.replace(/\s+/g, "-").toLowerCase();

    useEffect(() => {
        const handleScroll = () => {
            if (window.location.hash === `#${id}`) {
                const element = document.getElementById(id);
                if (element) {
                    element.scrollIntoView({
                        behavior: "smooth",
                        block: "center",
                    });
                }
            }
        };

        handleScroll();

        window.addEventListener("hashchange", handleScroll);

        return () => {
            window.removeEventListener("hashchange", handleScroll);
        };
    }, [id]);

    return (
        <div>
            <code id={id} className="api-name-header">{name}</code>
            <br />
            <PropSingle fullName={fullName} type={type} desc={desc} />
        </div>
    );
};

export default Prop;